  --primary-color: wheat;
  --secondary-color: green;
}

* {
  box-sizing: border-box;
}

body,
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    background: var(--primary-color);
    text-decoration: none;
}

/* App bar */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 5%;
    background: var(--secondary-color);
}

.menu {
    font-size: 32px;
    margin: 10px;
    display: block;
    min-width: 50px;
    min-height: 44px;
    cursor: pointer;
    color: var(--primary-color);
    background-color: var(--secondary-color);
    text-decoration: none;
    border: none;
}

.nav-title{
  font-size: 16px;
  padding: 0.6rem;
  margin: 0;
  min-width: 44px;
  min-height: 44px;
}

.nav-item,
a,
ul {
  text-decoration: none;
  list-style: none;
  color: var(--primary-color);
}

.nav-item {
  box-sizing: border-box;
  line-height: 24px;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;  
}

.nav-item a {
  display: inline-block;
  padding: 0.7rem;
  font-size: 1.2em;
  min-width: 44px;
  min-height: 44px;  
}

.nav-list {
    width: 100%;
    padding: 0;
    margin: 0;
}

.nav a:hover {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border-radius: 10px;
    padding: 0.7rem;
    font-size: 1em;
}

/*Jumbotron*/
.jumbotron-headline{
  color: aliceblue;
  font-size: 40px;
}

.jumbotron-label{
  color: antiquewhite;
  font-size: 20px;
}

.jumbotron{
  display: flex;
  align-items: center;
  min-height: 500px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
}

.jumbotron-inner{
  margin: 0 auto;
  max-width: 800px;
}

.jumbotron-button{
  margin-top: 16px;
  padding: 1em;
  border: 10px;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

/*Main Content*/
.main-content-container{
  width: 100%;
  margin: 60px auto;
  padding: 0 5%;
  text-align: center;
}

.explore-restaurant{
  font-size: 32px;
  text-align: center;
}

#explore-restaurant-list{
  display: grid;
  grid-row-gap: 1.5em;
  padding-top: 1.5em;
}

.explore-restaurant-label{
  font-size: 32px;
  text-align: center;
}

/* card */
.card {
  width: 100%;
  text-align: center;
  transition: 0.3s;
  background-color: var(--primary-color);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 1em;
}

.card-rating {
  position: absolute;
  top: 0;
  right: 0;
  color: var(--primary-color);
  font-size: large;
  background-color: var(--secondary-color);
  padding: 0.4em 1.3em;
  z-index: 1;
  border-radius: 0 1em;

}

.card-rating .fa {
  font-size: smaller;
  padding: 0.3em 0.8em 0.3em 0;
  color: orange;
}

.card-img-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  transition: transform 0.5s ease;
  max-height: 600px;
}

.card-image {
  object-fit: cover;
  height: 250px;
  width: 100%;
  border-radius: 1em 1em 0 0;
}

.card-content {
  padding: 1em 2em;
  font-size: small;
  text-align: center;
}

.card-content-title {
  font-weight: bold;
  padding-bottom: 0.4em;
}
.card-content-title a {
  padding: 1em 0;
  display: inline-block;
  color: var(--secondary-color);
  min-height: 44px;
  min-width: 44px;
}

.card-content-title-description {
  text-align: left;
  font-weight: bold;
  font-size: large;
  margin: 0;
}

.card-content-description {
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin: 10px 0;
}

/*detail*/
.detail {
  display: grid;
  grid-gap: 1.545em;
  font-size: 1em;
  margin-top: 1em;
}

.detail-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.detail-name-address-rating {
  display: inline-block;
  margin: 0;
}

.icon-primary {
  color: var(--secondary-color);
}

.detail h3 {
  font-size: 1.5em;
  padding: 1em 0;
  background-color: var(--secondary-color);
  font-weight: bold;
  color: var(--primary-color);
  margin: 0;
  border-radius: 0.5em;
}

.detail-desc {
  text-align: justify;
  color: var(--secondary-color);
  margin-bottom: 0.5em;
  margin: 0;
}

.category {
  padding: 0.3em 1em;
  color: var(--primary-color);
  background-color: var(--secondary-color);
  margin-right: 2px;
  border: 1px solid transparent;
  border-radius: 0px 10px 0px 10px;
  text-align: left;
}

.detail-menu h4 {
  font-weight: bold;
  font-size: 1.5em;
  padding: 1em;
  color: whitesmoke;
  background-color: blue;
  margin: 0;
}

.detail-food {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
}

.detail-food ul,
.detail-drink ul {
  color: var(--secondary-color);
  padding-left: 0;
}

.detail-food li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  background-color: var(--primary-color);
  border: 0.5px solid var(--secondary-color);
  border-width: 0 0 0.5px;
  font-size: 1.37em;
}

.detail-drink {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
}

.detail-drink li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  background-color: var(--primary-color);
  border: 0.5px solid var(--secondary-color);
  border-width: 0 0 0.5px;
  font-size: 1.37em;
}

.detail-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 0;
  margin-bottom: 0;
}

.detail-info h4 {
  color: var(--secondary-color);
  font-size: 1.5em;
  margin: 0;
  text-align: left;
  padding-left: 0.5em;
}

.detail-info li {
  position: relative;
  display: block;
  padding: 0.6em;
  color: var(--secondary-color);
  text-decoration: none;
  background-color: var(--primary-color);
}

.resto-name,
.resto-address,
.resto-rating,
.resto-category {
  font-size: 1.5em;
  margin: 0;
  text-align: left;
}

/*review*/
.detail-review {
  max-width: 100%;
  font-size: 1em;
  text-align: center;
}

.detail-review-item {
  color: var(--secondary-color);
  background-color: var(--primary-color);
  margin-bottom: 1.5em;
  padding: 1em 1em;
  border: 1px solid var(--secondary-color);
  border-radius: 2em;
}

.detail-review-item:last-child {
  margin-bottom: 0;
}

.header-review {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em;
  color: var(--secondary-color);
  border-bottom: 1px solid var(--secondary-color);
}

.name-review {
  font-weight: bold;
  display: flex;
  font-size: 1.5em;
  margin: 0;
}

.date-review {
  font-size: 1em;
  font-weight: lighter;
  font-size: 1em;
}

.body-review {
  padding: 1em;
  text-align: left;
  font-size: 1.37em;
}

/*favorite*/
.like {
  font-size: 18px;
  background-color: blue;
  color: var(--primary-color);
  border: 0;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 16px;
  right: 16px;
  z-index: 10;
}


/*footer*/
footer{
  background-color: var(--secondary-color);
  color: aliceblue;
  font-size: 16px;
  padding: 1em;
  width: 100%;
  text-align: center;
}

/*form review*/
form{
  margin: 1em;
  padding: 2em;
  border-radius: 15px;
}

.form-control {
  display: inline-block;
  width: 100%;
  color: var(--secondary-color);
  border-radius: 1em;
  min-height: 100px;
  min-width: 44px;
  padding: 0.3em 0.5em;
}

.form-label {
  padding: 0.5em 0;
  display: inline-block;
  font-size: 1.4em;
  color: var(--secondary-color);
  min-height: 44px;
  min-width: 44px;
}

.mb3 {
  margin-top: 1em;
  margin-bottom: 1em;
}

.btn {
  display: inline-block;
  min-height: 44px;
  min-width: 44px;
  padding: 0.5em;
  margin-top: 1.5em;
}

/*skip link*/
.skip-link{
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100; 
}

.skip-link:focus{
  top: 0;
}

