ze: 20px;\n}\n\n.jumbotron{\n  display: flex;\n  align-items: center;\n  min-height: 500px;\n  width: 100%;\n  text-align: center;\n  background-image: url(\"../public/images/heros/hero-image_4.jpg\");\n  background-position: center;\n}\n\n.jumbotron-inner{\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.jumbotron-button{\n  margin-top: 16px;\n  padding: 1em;\n  border: 10px;\n  background-color: var(--secondary-color);\n  color: var(--primary-color);\n  font-size: 14px;\n  font-weight: 500;\n  cursor: pointer;\n}\n\n/*Main Content*/\n.main-content-container{\n  width: 100%;\n  margin: 60px auto;\n  padding: 0 5%;\n  text-align: center;\n}\n\n.explore-restaurant{\n  font-size: 32px;\n  text-align: center;\n}\n\n#explore-restaurant-list{\n  display: grid;\n  grid-row-gap: 1.5em;\n  padding-top: 1.5em;\n}\n\n.explore-restaurant-label{\n  font-size: 32px;\n  text-align: center;\n}\n\n/* card */\n.card {\n  width: 100%;\n  text-align: center;\n  transition: 0.3s;\n  background-color: var(--primary-color);\n  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);\n  border-radius: 1em;\n}\n\n.card-rating {\n  position: absolute;\n  top: 0;\n  right: 0;\n  color: var(--primary-color);\n  font-size: large;\n  background-color: var(--secondary-color);\n  padding: 0.4em 1.3em;\n  z-index: 1;\n  border-radius: 0 1em;\n\n}\n\n.card-rating .fa {\n  font-size: smaller;\n  padding: 0.3em 0.8em 0.3em 0;\n  color: orange;\n}\n\n.card-img-container {\n  width: 100%;\n  position: relative;\n  overflow: hidden;\n  transition: transform 0.5s ease;\n  max-height: 600px;\n}\n\n.card-image {\n  object-fit: cover;\n  height: 250px;\n  width: 100%;\n  border-radius: 1em 1em 0 0;\n}\n\n.card-content {\n  padding: 1em 2em;\n  font-size: small;\n  text-align: center;\n}\n\n.card-content-title {\n  font-weight: bold;\n  padding-bottom: 0.4em;\n}\n.card-content-title a {\n  padding: 1em 0;\n  display: inline-block;\n  color: var(--secondary-color);\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.card-content-title-description {\n  text-align: left;\n  font-weight: bold;\n  font-size: large;\n  margin: 0;\n}\n\n.card-content-description {\n  text-align: left;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  margin: 10px 0;\n}\n\n/*detail*/\n.detail {\n  display: grid;\n  grid-gap: 1.545em;\n  font-size: 1em;\n  margin-top: 1em;\n}\n\n.detail-img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  object-position: center;\n}\n\n.detail-name-address-rating {\n  display: inline-block;\n  margin: 0;\n}\n\n.icon-primary {\n  color: var(--secondary-color);\n}\n\n.detail h3 {\n  font-size: 1.5em;\n  padding: 1em 0;\n  background-color: var(--secondary-color);\n  font-weight: bold;\n  color: var(--primary-color);\n  margin: 0;\n  border-radius: 0.5em;\n}\n\n.detail-desc {\n  text-align: justify;\n  color: var(--secondary-color);\n  margin-bottom: 0.5em;\n  margin: 0;\n}\n\n.category {\n  padding: 0.3em 1em;\n  color: var(--primary-color);\n  background-color: var(--secondary-color);\n  margin-right: 2px;\n  border: 1px solid transparent;\n  border-radius: 0px 10px 0px 10px;\n  text-align: left;\n}\n\n.detail-menu h4 {\n  font-weight: bold;\n  font-size: 1.5em;\n  padding: 1em;\n  color: whitesmoke;\n  background-color: blue;\n  margin: 0;\n}\n\n.detail-food {\n  display: flex;\n  flex-direction: column;\n  text-align: center;\n  align-content: center;\n}\n\n.detail-food ul,\n.detail-drink ul {\n  color: var(--secondary-color);\n  padding-left: 0;\n}\n\n.detail-food li {\n  padding: 0.5em 0;\n  display: block;\n  text-decoration: none;\n  background-color: var(--primary-color);\n  border: 0.5px solid var(--secondary-color);\n  border-width: 0 0 0.5px;\n  font-size: 1.37em;\n}\n\n.detail-drink {\n  display: flex;\n  flex-direction: column;\n  text-align: center;\n  align-content: center;\n}\n\n.detail-drink li {\n  padding: 0.5em 0;\n  display: block;\n  text-decoration: none;\n  background-color: var(--primary-color);\n  border: 0.5px solid var(--secondary-color);\n  border-width: 0 0 0.5px;\n  font-size: 1.37em;\n}\n\n.detail-info {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n  padding-left: 0;\n  margin-bottom: 0;\n}\n\n.detail-info h4 {\n  color: var(--secondary-color);\n  font-size: 1.5em;\n  margin: 0;\n  text-align: left;\n  padding-left: 0.5em;\n}\n\n.detail-info li {\n  position: relative;\n  display: block;\n  padding: 0.6em;\n  color: var(--secondary-color);\n  text-decoration: none;\n  background-color: var(--primary-color);\n}\n\n.resto-name,\n.resto-address,\n.resto-rating,\n.resto-category {\n  font-size: 1.5em;\n  margin: 0;\n  text-align: left;\n}\n\n/*review*/\n.detail-review {\n  max-width: 100%;\n  font-size: 1em;\n  text-align: center;\n}\n\n.detail-review-item {\n  color: var(--secondary-color);\n  background-color: var(--primary-color);\n  margin-bottom: 1.5em;\n  padding: 1em 1em;\n  border: 1px solid var(--secondary-color);\n  border-radius: 2em;\n}\n\n.detail-review-item:last-child {\n  margin-bottom: 0;\n}\n\n.header-review {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 0.5em;\n  color: var(--secondary-color);\n  border-bottom: 1px solid var(--secondary-color);\n}\n\n.name-review {\n  font-weight: bold;\n  display: flex;\n  font-size: 1.5em;\n  margin: 0;\n}\n\n.date-review {\n  font-size: 1em;\n  font-weight: lighter;\n  font-size: 1em;\n}\n\n.body-review {\n  padding: 1em;\n  text-align: left;\n  font-size: 1.37em;\n}\n\n/*favorite*/\n.like {\n  font-size: 18px;\n  background-color: blue;\n  color: var(--primary-color);\n  border: 0;\n  border-radius: 50%;\n  width: 55px;\n  height: 55px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  z-index: 10;\n}\n\n\n/*footer*/\nfooter{\n  background-color: var(--secondary-color);\n  color: aliceblue;\n  font-size: 16px;\n  padding: 1em;\n  width: 100%;\n  text-align: center;\n}\n\n/*form review*/\nform{\n  margin: 1em;\n  padding: 2em;\n  border-radius: 15px;\n}\n\n.form-control {\n  display: inline-block;\n  width: 100%;\n  color: var(--secondary-color);\n  border-radius: 1em;\n  min-height: 100px;\n  min-width: 44px;\n  padding: 0.3em 0.5em;\n}\n\n.form-label {\n  padding: 0.5em 0;\n  display: inline-block;\n  font-size: 1.4em;\n  color: var(--secondary-color);\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.mb3 {\n  margin-top: 1em;\n  margin-bottom: 1em;\n}\n\n.btn {\n  display: inline-block;\n  min-height: 44px;\n  min-width: 44px;\n  padding: 0.5em;\n  margin-top: 1.5em;\n}\n\n/*skip link*/\n.skip-link{\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: #bf1722;\n  color: white;\n  padding: 8px;\n  z-index: 100; \n}\n\n.skip-link:focus{\n  top: 0;\n}\n\n"],"sourceRoot":""}]);
